<template>
  <p class="body-2 grey--text text--darken-2">
    <v-icon>timer</v-icon>
    {{ days | two_digits }} : {{ hours | two_digits }} : {{ minutes | two_digits }} : {{ seconds | two_digits }}
  </p>
</template>

<script>
export default {
  props: ["date"],
  data() {
    return {
      now: Math.trunc(new Date().getTime() / 1000),
      deadline: ""
    };
  },
  mounted() {
    let interval;
    if (this.date) {
      this.deadline = new Date(this.date).getTime() / 1000;
      if (this.now < this.deadline) {
        this.$nextTick(() => {
          interval = setInterval(() => {
            this.now = Math.trunc(new Date().getTime() / 1000);
          }, 1000);
        });
      } else {
        clearInterval(interval);
      }
    } else {
      this.deadline = 0;
    }
  },
  computed: {
    seconds() {
      let s = (this.deadline - this.now) % 60;
      return s > 0 ? s : 0;
    },

    minutes() {
      let m = Math.trunc((this.deadline - this.now) / 60) % 60;
      return m > 0 ? m : 0;
    },

    hours() {
      let h = Math.trunc((this.deadline - this.now) / 60 / 60) % 24;
      return h > 0 ? h : 0;
    },

    days() {
      let d = Math.trunc((this.deadline - this.now) / 60 / 60 / 24);
      return d > 0 ? d : 0;
    }
  },
  filters: {
    two_digits(value) {
      if (value.toString().length <= 1) {
        return "0" + value.toString();
      }
      return value.toString();
    }
  }
};
</script>



